import React from 'react';
// react here is the view library
import ReactDOM from 'react-dom';
// reactDOM is used for websites
// react native is for mobile phones.
import {Provider} from 'react-redux';

import { createStore, applyMiddleware, combineReducers } from 'redux';

import {createLogger} from 'redux-logger'; // its a middleware, we can apply this to our app


import thunkMiddleware from 'redux-thunk';

import './index.css';
// import Card from './Card';

import * as serviceWorker from './serviceWorker';
// SERVICE worker is a new feature that makes our app performance faster and allows us to use offline
import 'tachyons';
// we can easily insert css styles thru assigning of classes.
import App from './containers/App';
import { requestRobots, searchRobots } from './reducers';
// we are having one big component caleld App, which will be the father of all our child components like cardlist, header ,etc.


const logger = createLogger() // logger function is a middleware and we can apply it to our redux app.

const rootReducer = combineReducers({requestRobots, searchRobots})

const store = createStore(rootReducer, applyMiddleware(thunkMiddleware, logger))
// goes thru the tthunk middleware then thhe logger middleware. 


ReactDOM.render(
  <React.StrictMode>
    <Provider store = {store}>
      <App />
    </Provider> 
  </React.StrictMode>,
  document.getElementById('root')
);
// Provider component wraps the App component
// by passing store prop to provider comp, it will pass it down to all the components

// CardList is accepting a robots prop.

// the reason why we can use import here is coz react has webpack underneath the hood that does the bundling for us.
// we are grabbing the id root in index.js file, and we will render(aka display) the react component CardList.
// Within the CardList component, we can add properties(shortform is props),such as robots.
// If you want your app to work offline and load faster, you can change unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
// greeting is a property we have set. now we can access it on hello.js
// BTW, service worker is meant for progressive web apps, which we dont need now hence its default is unregister.

serviceWorker.unregister();


// named export vs default export - https://medium.com/@etherealm/named-export-vs-default-export-in-es6-affb483a0910
