import React, {Component} from 'react';
import {connect} from 'react-redux';
import CardList from '../components/CardList';
// theres no .js at the back of cardlist because js automatically assumes its js if theres no code at the back.
// purpose of CardList component - one way data flow - hence we can have one big cardlist component with child components called card.
import './App.css';
// importing the css file for App.
import SearchBox from '../components/SearchBox';
import Scroll from '../components/Scroll'
import ErrorBoundary from  '../components/ErrorBoundary';
// .. means leave the existing folder, go to the components folder
// import {robots} from './robots';
// importing the robots array
// technically we could have done import robots from './robots' but since in robots.js we used export const instead of export default, robots.js could have multiple exports
//
// hence the rule is for those which arent default, we have to destructure it.
// we need destructure this because its not default

// how do we make the search box and cardlist communicate w each other? so that we can display the filtered robots?
// one way data flow - if children, cardlist and searcch box want to comm w each other, searchbox need to send info to parent, which will then pass it down to the CardList
// react has an idea of state
// we can pass down props down from one comp to its child comp.(1 way data flow)
// STATE - simply means the description of our app, an obj that describes our app
// parent feeds state to child component as props. STATE >> props

// state in this case is the robots and whatever is typed in search SearchBox
// in order to use state, we need to create a class instead of function.

//HOW TO ADD STATE? we need use a constructor function, inside we declare the state.
// in order to use this, we need put super() in front,which calls the constructor of component.
//state is smth that can always change and affect our App, and they usually live in the parent component,so that the parent can pass the state to diff components later on.
import {setSearchField, requestRobots} from '../actions'
const mapStateToProps = state => {
  return {
    searchField : state.searchRobots.searchField,
    robots: state.requestRobots.robots,
    isPending : state.requestRobots.isPending,
    error: state.requestRobots.error
  }
}
// this is gonna be used as props by the App component 

// the searchField that we are gonna return that is gonna be used as props is going to come from the state.searchRobots.searchField.
const mapDispatchToProps = (dispatch) => {
  return {
    onSearchChange: (event) => dispatch(setSearchField(event.target.value)),
    onRequestRobots: () => dispatch(requestRobots())
  }
}
// onSearchChange is a prop
// dispatch is what triggers the sending of action 

class App extends Component {
  // constructor (){
  //   super()
  //   this.state = {
  //     robots : []
  //   }
  // }
// componentDidMount(){ // making a request .
//   // console.log(this.props.store.getState())
//   fetch('https://jsonplaceholder.typicode.com/users')
//   .then(response =>response.json())
//   .then(users=> this.setState({robots:users}))
// } // fetch is a property of window obj

componentDidMount(){
  this.props.onRequestRobots()
}


  render(){
    // const {robots} = this.state
    const {searchField, onSearchChange , robots, isPending} = this.props
    const filteredRobots = robots.filter(robot =>{
    return robot.name.toLowerCase().includes(searchField.toLowerCase())
    })
    return isPending ? // can also do !robots.length as robots.length initially 0,which is gon be false,turn it into true using !
    <h1>Loading</h1> : // sort of a loading bar.
      ( // using ternary operator here in place of if else statement.
      <div className = "tc">
        <h1 className = "f1">RoboFriends</h1>
        <SearchBox searchChange={onSearchChange}/>
        <Scroll>
          <ErrorBoundary>
            <CardList robots = {filteredRobots}/>
          </ErrorBoundary>
        </Scroll>
      </div>
    )
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(App);
// connect is a higher order function . connect is gonna run , and whatever it does inside the function, it reqturns another function and it 
// connect is going to run, and it will return another function , which will take in App as a argument.
// we are asking app to subsribe to any changes 

// class always has a RENDER function that has to RETURN smth.



// STATE >> props
// props are inputs that we always get and they never change
// props are simply things that come out state
// STATE :
// its an object that describes our app



// how to add state: we use a constructor function , and delcare state inside it. in order to use 'this', we need use super() which calls the constructor of component.
