import {
    CHANGE_SEARCH_FIELD,
    REQUEST_ROBOTS_PENDING,
    REQUEST_ROBOTS_SUCCESS,
    REQUEST_ROBOTS_FAILED
} from './constant.js'

const initialStateSearch = {
    searchField : '',

}

// creating a reducer 
// reducer takes in 2 arguments, current state and action.

export const searchRobots = (state = initialStateSearch, action = {}) => {
    switch(action.type){
        case CHANGE_SEARCH_FIELD:
            return Object.assign({}, state, {searchField: action.payload})
// Object.assign(target, source, source 1... ) - assigns sources to target.

// we are returning a new obj which has everythn iin the state plus the new searchField property 
// we received an action called change search field then return new state w action.payload
// return {...state,searchField:action.payload} - alt method
        default:
            return state;
    }
}

// reducer reads the action and returns the state.

// if we receive a action called change search field, then we return a new state that has everythn in the state, and update the search field property w the action.payload 
const initialStateRobots = {
    isPending : false, 
    robots : [],
    error: ''
}


export const requestRobots = (state=initialStateRobots, action = {}) => {
    switch(action.type){
        case REQUEST_ROBOTS_PENDING:
            return Object.assign({},state,{isPending : true})
       
        case REQUEST_ROBOTS_SUCCESS:
            return Object.assign({},state,{robots: action.payload , isPending: false})
       
        case REQUEST_ROBOTS_FAILED:
            return Object.assign({},state,{error:action.payload, isPending: false })
  
        default:
            return state
        }
}

