import React from 'react';
import Card from './Card'
//card is a default export so theres no need for {} around Card.

const CardList = ({robots}) => {
  return (
    <div>
      {
        robots.map((user,i) => {
          return (
            <Card
              key={i}
              id={robots[i].id}
              name={robots[i].name}
              email ={robots[i].email}
              />
        )
      })
    }
    </div>

  );
}
export default CardList;
// anything within {} is javascript.
// we need to create a key prop whenever we are doing a loop. Each child in a array shld have a unique "key" prop . - special case.
// the key is needed so that react can easily find in its virtual dom, which card to remove , and just render it(by removing it), rather than having to render the whole array of cards.
// note!!!: key prop is recommended to be something that doesnt change . For example index i could change if array items get moved/deleted.So a better key would be smth unique like id.
// we are basically looping over the robots array and creating a card component for each user element in the array.
// we can access the robots property and whatever property value it has from index.js !!
// We are basically creatiing functions which take in parameters
// these parameters are given by giving attributes/values/properties to CardList component in index.js
