import React from 'react';
// React must be in scope when using jsx.
// jsx is the html like syntax inside return
const Card = ({name,email,id}) => {
  return(
    <div className='tc grow bg-light-green br3 pa3 ma2 dib bw2 shadow-5'>
      <img alt= "robots" src ={`https://robohash.org/${id}?size=200x200`}/>
      <div>
        <h2>{name}</h2>
        <p>{email}</p>
      </div>
    </div>

  )
}
export default Card;

//This is a default export.  One can have only one default export per file.

// javscript expression use curly brackets
// https://medium.com/@lcriswell/destructuring-props-in-react-b1c295005ce0 - destructuring of props
//Note: if we were using class Card extends Component - we need use render function to render smth and to do so we need to return a html code within it.
// note : we can only return one element. hence everything shld be wrapped in the div so that we just return the div.
// since we have tachyons, we can give these tachyons classes to div to style the card.
// Note : we are using backticks ' '
// line 3 : we are receiving the props in () and we are destructuring it into name,email,id
// Note: Every React component can be self-closing: <div />. <div></div> is also an equivalent.
// In React's JSX, you only need to write <MyComponent></MyComponent> when the component has child components!!.
// JSX allows us to write html like syntax in a js file.
