import React, {Component} from 'react';

class ErrorBoundary extends Component {
  constructor(props){
    super(props);
    this.state = {
      hasError:false
    }

  }
  componentDidCatch(error,info){ // lifecycle method .
    this.setState({ hasError : true})
  }

  render(){
    if (this.state.hasError){
      return <h1>Oooops. That is not good </h1>
    }
    return this.props.children
  }
}



export default ErrorBoundary;

//can warp this around other compoonents.
// <ErrorBoundary> <card> </ErrorBoundary>
// Error boundaries are v useful esp in production mode, as if its child component fails to render, it will easily render a h1 tag saying smth's wrong.
// however in dev mode, we cant see it, instead in console it will show whr the error occured aka the cardlist component.
