import React from 'react';

const SearchBox = ({searchfield,searchChange}) => {
  return (
    <div className = 'pa2'>
      <input
      className = 'pa3 ba b--green bg-lightest-blue'
      type ='search'
      placeholder = 'search robots'
      onChange = {searchChange}

      />
    </div>

  )
}

export default SearchBox


// everytime the onchange event is triggered, call the searchchange function.
// searchchange function which is a prop , is the onSearchChange function triggered in the App.js.
// then the onSearchChange function in app gets executed.
