import React from 'react';
// we are making a scrollable component here. Can reuse for other purposes as well.
const Scroll = (props) => {
  return (
    <div style = {{overflowY: 'scroll', border:'5px solid black',height:'800px'}}>
      {props.children}
    </div>
  )
}

export default Scroll;

// in JSX u can add styles by using double curly brackets. {{}}
// rmb to CamelCase for JsX : css : overflow - x jsx: overflowX
