// text is what the user inputs
// returns a object that has a type, and payload.


// Creating an ACTION

import {
    CHANGE_SEARCH_FIELD,
    REQUEST_ROBOTS_PENDING,
    REQUEST_ROBOTS_SUCCESS,
    REQUEST_ROBOTS_FAILED
} from './constant.js'
// a string is assigned to the type. hence to prevent typos, use a constant instead to hold the value of string.


export const setSearchField = (text) => ({
    type : CHANGE_SEARCH_FIELD, // iits a constant and a const is usually caps. 
    payload: text 
 
})
// As you can see it's a JavaScript object with two properties: type and payload.

// The type property drives how the state should change
//  and it's always required by Redux. The payload property instead describes what should change, 
// and might be omitted if you don't have new data to save in the store.
// and payload is gonna send the text (which the user inputted) to the reducer

// higher order function - a function that returnrs a function. 

export const requestRobots = () => (dispatch) => {
    dispatch({type:REQUEST_ROBOTS_PENDING});
    fetch('https://jsonplaceholder.typicode.com/users')
    .then(response =>response.json())
    .then(data => dispatch({type:REQUEST_ROBOTS_SUCCESS, payload : data}))
    .catch(error => dispatch({type:REQUEST_ROBOTS_FAILED, payload: error}))

}